import React, { ReactElement, useState, useEffect } from "react";
import {
  Modal,
  Spinner,
  Button,
  Dropdown
} from "react-bootstrap";
import { Auth } from "aws-amplify";
import Timer from "./Timer/Timer";
import EndLab from "./EndLab/EndLab";
import Finish from "./Finish/Finish";
import Scenario from "./Scenario/Scenario";
import PreLab from "./PreLab/PreLab";
import Home from "./Home/Home";
import SignUp from "./SignUp/SignUp";
import SignIn from "./SignIn/SignIn";
import ConfirmSignUp from "./ConfirmSignUp/ConfirmSignUp";
import BackendAPI from "./api";
import "./App.scss";

const App = () : ReactElement => {
  const [view, setView] = useState("SignIn");
  const [modalShow, setModalShow] = useState(false);
  const [confirmDisabled, setConfirmDisabled] = useState(false);
  const [accountId, setAccountId] = useState("");
  const [email, setEmail] = useState("");
  const [createDate, setCreateDate] = useState("");
  const [currentState, setCurrentState] = useState("");
  const [signIn, setSignIn] = useState(false);
  const [progress, setProgress] = useState(0);

  useEffect(() => {
    Auth.currentSession()
      .then((response) => {
        console.log(response);
        setEmail(response.getAccessToken().payload.username);
        setSignIn(true);
        setView("Home");
      })
      .catch((error) => {
        console.log(error);
      });
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const modalHandleClose = () => {
    setModalShow(false);
  };

  const signOut = () => {
    Auth.signOut()
      .then(() => {
        setAccountId("");
        setEmail("");
        setCreateDate("");
        setCurrentState("");
        setSignIn(false);
        setProgress(0);
        setView("SignIn");
      });
  };

  const endLabHandler = () => {
    setConfirmDisabled(true);
    BackendAPI.TearDownEnvironment()
      .then(() => {
        setCurrentState("terminating");
        modalHandleClose();
        setView("EndLab");
      });
  };

  const endLabLabel = currentState === "terminating" ? "Ending Lab" : "End Lab";

  return (
    <div className="app">
      <Modal show={modalShow} onHide={modalHandleClose}>
        <Modal.Header closeButton>
          <Modal.Title>Are you sure?</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          This action will revoke your access to the lab, and tear down the lab environment. It will take a while before you can redo the lab.
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={modalHandleClose}>
            Close
          </Button>
          <Button variant="danger" onClick={endLabHandler} disabled={confirmDisabled}>
            {
              confirmDisabled
              && (
              <Spinner
                as="span"
                animation="border"
                size="sm"
                role="status"
                aria-hidden="true"
              />
              )
            }
            {" "}
            Confirm
          </Button>
        </Modal.Footer>
      </Modal>
      <div className="app-header">
        {
          accountId
          && (
            <>
              <div className="left-header">
                <Button
                  onClick={() => setModalShow(true)}
                  variant="danger"
                  disabled={currentState === "terminating"}
                >
                  {endLabLabel}
                </Button>
                {
                  currentState !== "terminating"
                  && (
                  <Timer
                    setView={setView}
                    setCurrentState={setCurrentState}
                    createDate={createDate}
                  />
                  )
                }
              </div>
              <span className="info-link">
                Lab URL :
                {" "}
              </span>
              <Button
                variant="link"
                className="link"
                href={`https://${accountId}.signin.aws.amazon.com/console`}
                target="_blank"
              >
                https://
                {accountId}
                .signin.aws.amazon.com/console
              </Button>
            </>
          )
        }
        {
          signIn
          && (
          <>
            <span className="info-link">
              Account :
              {" "}
            </span>
            <Dropdown>
              <Dropdown.Toggle className="link">
                {email}
              </Dropdown.Toggle>
              <Dropdown.Menu>
                <Dropdown.Item onClick={signOut}>Sign Out</Dropdown.Item>
              </Dropdown.Menu>
            </Dropdown>
          </>
          )
        }
      </div>
      <div className="app-body">
        {view === "Home"
        && (
        <Home
          setView={setView}
          setAccountId={setAccountId}
          setCurrentState={setCurrentState}
          setCreateDate={setCreateDate}
          setProgress={setProgress}
        />
        )}
        {view === "SignIn"
        && (
        <SignIn
          setView={setView}
          setEmail={setEmail}
          setSignIn={setSignIn}
        />
        )}
        {view === "SignUp"
        && (
        <SignUp
          setView={setView}
          setEmail={setEmail}
        />
        )}
        {view === "ConfirmSignUp" && <ConfirmSignUp setView={setView} email={email} />}
        {view === "PreLab" && <PreLab setView={setView} />}
        {
          view === "Scenario"
          && (
          <Scenario
            setView={setView}
            progress={progress}
            accountId={accountId}
            setCurrentState={setCurrentState}
          />
          )
        }
        {view === "Finish" && <Finish />}
        {view === "EndLab" && <EndLab />}
      </div>
    </div>
  );
};

export default App;
