import { Auth, API } from "aws-amplify";

enum Method {
  Get = "get",
  Post = "post"
}

const InvokeAPI = (
  method: Method,
  endpoint: string,
  path: string,
  body?: Record<string, unknown>
): Promise<any> => Auth.currentSession()
  .then((session) => API[method](
    endpoint,
    path,
    {
      headers: {
        Authorization: `Bearer ${session.getIdToken().getJwtToken()}`
      },
      body
    }
  ));

// const TearDownEnvironment = (): Promise<any> => Promise.resolve("");
const TearDownEnvironment = (): Promise<any> => InvokeAPI(Method.Post, "CloudTrain", "/v1/tear-down-environment");
const UpdateParticipantResult = (): Promise<any> => InvokeAPI(Method.Post, "CloudTrain", "/v1/update-participant-result");
const GetParticipantResult = (): Promise<any> => InvokeAPI(Method.Get, "CloudTrain", "/v1/participant-result");
const GetAccountAvailability = (): Promise<any> => InvokeAPI(Method.Get, "CloudTrain", "/v1/account-availability");
const AssignParticipantToAccount = (): Promise<any> => InvokeAPI(Method.Post, "CloudTrain", "/v1/assign-participant-to-account");
const AssignGroupToParticipant = (): Promise<any> => InvokeAPI(Method.Post, "CloudTrain", "/v1/assign-group-to-participant");

export default {
  Method,
  InvokeAPI,
  TearDownEnvironment,
  UpdateParticipantResult,
  GetParticipantResult,
  GetAccountAvailability,
  AssignParticipantToAccount,
  AssignGroupToParticipant
};
