import React, { ReactElement, FunctionComponent, useState } from "react";
import {
  Button,
  Form,
  Spinner
} from "react-bootstrap";
import { Auth } from "aws-amplify";

const SignIn: FunctionComponent<{
  setView: (val: string) => void,
  setEmail: (val: string) => void,
  setSignIn: (val: boolean) => void
}> = ({
  setView, setEmail, setSignIn
}): ReactElement => {
  const [tempEmail, setTempEmail] = useState("");
  const [password, setPassword] = useState("");
  const [signInError, setSignInError] = useState("");
  const [sigingIn, setSigingIn] = useState(false);

  const signInDisabled = !tempEmail || !password || sigingIn;

  const signIn = () => {
    setSigingIn(true);
    return Auth.signIn({
      username: tempEmail,
      password
    })
      .then((response) => {
        console.log(response);
        setEmail(tempEmail);
        setSignIn(true);
        setView("Home");
      })
      .catch((error) => {
        console.log("error signing up:", error);
        if (error.code === "UserNotConfirmedException") {
          setEmail(tempEmail);
          setView("ConfirmSignUp");
        } else {
          setSignInError(error.message);
          setSigingIn(false);
        }
      });
  };

  return (
    <Form>
      <h3>Sign In</h3>

      <Form.Group>
        <span className="info-link">
          Do not have an account?
          {" "}
        </span>
        <Button onClick={() => setView("SignUp")} className="link">Register</Button>
      </Form.Group>

      <Form.Group className="form-group">
        <Form.Label>Email address</Form.Label>
        <Form.Control
          type="email"
          className="form-control"
          placeholder="Enter email"
          onChange={(e) => setTempEmail(e.target.value)}
        />
      </Form.Group>

      <Form.Group className="form-group">
        <Form.Label>Password</Form.Label>
        <Form.Control
          type="password"
          className="form-control"
          placeholder="Enter password"
          onChange={(e) => setPassword(e.target.value)}
        />
      </Form.Group>

      <Button
        onClick={signIn}
        disabled={signInDisabled}
      >
        {
          sigingIn
          && (
          <Spinner
            as="span"
            animation="border"
            size="sm"
            role="status"
            aria-hidden="true"
          />
          )
        }
        {" "}
        Submit
      </Button>

      <Form.Group>
        <Form.Label className="error">{signInError}</Form.Label>
      </Form.Group>
    </Form>
  );
};

export default SignIn;
