import React, {
  FunctionComponent, ReactElement, useState
} from "react";
import {
  Alert,
  Button,
  Table,
  Image,
  Spinner
} from "react-bootstrap";
import API from "../api";

const Base: FunctionComponent<{
  setView: (val: string) => void,
  name: string,
  description: string,
  steps: {"name": string, "details": string[]}[],
  accountId: string,
  endpoint: string,
  endpointPath: string,
  imgPath: string
}> = ({
  setView, name, description, steps, accountId, imgPath, endpoint, endpointPath
}): ReactElement => {
  const [valid, setValid] = useState([]);
  const [invalid, setinvalid] = useState([]);
  const [validateDisabled, setValidateDisabled] = useState(false);

  const validateScenario = () => {
    setValidateDisabled(true);
    return API.InvokeAPI(
      API.Method.Post,
      endpoint,
      endpointPath,
      { accountId }
    )
      .then((response) => {
        console.log(response);
        setValid(response.valid);
        setinvalid(response.invalid);
        setValidateDisabled(false);
      })
      .catch((error) => {
        console.log(`ERROR: ${error}`);
      });
  };

  return (
    <>
      <h3>
        {name}
      </h3>
      <div className="text-blob">
        {description}
      </div>
      <Image src={imgPath} alt="Architecture" fluid />
      <br />
      <Table
        className="info-table"
      >
        <thead>
          <tr>
            <td>S/N</td>
            <td>Step</td>
            <td>Details</td>
          </tr>
        </thead>
        <tbody>
          {
            (
              steps.map((step, index) => (
                <React.Fragment key={step.name}>
                  <tr>
                    <td rowSpan={step.details.length + 1}>{index + 1}</td>
                    <td rowSpan={step.details.length + 1}>{step.name}</td>
                  </tr>
                  {
                    (
                      step.details.map((detail) => (
                        <tr key={detail}>
                          <td>{detail}</td>
                        </tr>
                      ))
                    )
                  }
                </React.Fragment>
              ))
            )
          }
        </tbody>
      </Table>
      <br />
      <Button
        onClick={validateScenario}
        disabled={validateDisabled}
      >
        {
          validateDisabled
          && (
          <Spinner
            as="span"
            animation="border"
            size="sm"
            role="status"
            aria-hidden="true"
          />
          )
        }
        {" "}
        Validate
      </Button>
      <Button
        onClick={() => setView("Home")}
        disabled={valid.length === 0 || invalid.length > 0}
      >
        Next
      </Button>
      {
        (
          [...invalid].reverse().map((msg) => (
            <Alert key={msg} variant="warning">
              {msg}
            </Alert>
          ))
        )
      }
      {
        (
          [...valid].reverse().map((msg) => (
            <Alert key={msg} variant="success">
              {msg}
            </Alert>
          ))
        )
      }
      <br />
      <br />
    </>
  );
};

export default Base;

