import React, {
  FunctionComponent, ReactElement, useState, useEffect
} from "react";
import moment from "moment";
import config from "../config";
import API from "../api";
import "./Timer.scss";

interface Timer {
  hours: number,
  minutes: number,
  seconds: number
}

const TimerDefault = {
  hours: -1,
  minutes: -1,
  seconds: -1
};

const Timer: FunctionComponent<{
  setView: (val: string) => void,
  setCurrentState: (val: string) => void,
  createDate: string
}> = ({
  setView, setCurrentState, createDate
}): ReactElement => {
  const [timeLeft, setTimeLeft] = useState<Timer>({ ...TimerDefault });

  useEffect(() => {
    const triggerTearDown = () => (
      API.TearDownEnvironment()
        .then(() => {
          setCurrentState("terminating");
          setView("EndLab");
        })
    );

    const calculateTimeLeft = (): Timer => {
      const endTime = moment(createDate).add(config.Timer.duration, config.Timer.unit as moment.DurationInputArg2);
      const difference = moment.duration(endTime.diff(moment.now())).asSeconds();
      let timeRemaining = { ...TimerDefault };
      if (difference >= 0) {
        timeRemaining = {
          hours: Math.floor((difference / (60 * 60)) % 24),
          minutes: Math.floor((difference / 60) % 60),
          seconds: Math.floor((difference) % 60)
        };
      } else {
        triggerTearDown();
      }
      return timeRemaining;
    };

    const timesUp = () => {
      if (
        timeLeft.hours === 0
        && timeLeft.minutes === 0
        && timeLeft.seconds === 0
      ) {
        return true;
      }
      return false;
    };

    const timer = setTimeout(() => {
      console.log(timeLeft);
      setTimeLeft(calculateTimeLeft());
      if (timesUp()) {
        clearTimeout(timer);
        triggerTearDown();
      }
    }, 1000);
    return () => clearTimeout(timer);
  });

  const padLeadingZeros = (num: number, size: number): string => {
    let s = `${num}`;
    while (s.length < size) s = `0${s}`;
    return s;
  };

  return (
    <>
      {
        timeLeft.hours !== -1 && (
        <span className="timer" data-testid="timer">
          {padLeadingZeros(timeLeft.hours, 2)}
          {" : "}
          {padLeadingZeros(timeLeft.minutes, 2)}
          {" : "}
          {padLeadingZeros(timeLeft.seconds, 2)}
        </span>
        )
      }
    </>
  );
};

export default Timer;

