import React, { ReactElement, FunctionComponent, useState } from "react";
import {
  Button,
  Form
} from "react-bootstrap";
import { Auth } from "aws-amplify";

const SignUp: FunctionComponent<{
  setView: (val: string) => void,
  setEmail: (val: string) => void
}> = ({ setView, setEmail }) : ReactElement => {
  const [tempEmail, setTempEmail] = useState("");
  const [password, setPassword] = useState("");
  const [rePassword, setRePassword] = useState("");
  const [signUpError, setSignUpError] = useState("");

  const submitDisabled = !tempEmail || !password || !rePassword || password !== rePassword;

  const signUp = () => {
    Auth.signUp({
      username: tempEmail,
      password,
      attributes: {
        email: tempEmail
      }
    })
      .then((response) => {
        console.log(response);
        setView("ConfirmSignUp");
        setEmail(tempEmail);
      })
      .catch((error) => {
        console.log("error signing up:", error);
        setSignUpError(error.message);
      });
  };

  return (
    <Form className="form">
      <h3>Sign Up</h3>
      <Form.Group>
        <span className="info-link">
          Already have an account?
          {" "}
        </span>
        <Button onClick={() => setView("SignIn")} className="link">Sign In</Button>
      </Form.Group>
      <Form.Group>
        <Form.Label>Email address</Form.Label>
        <Form.Control type="email" placeholder="Enter email" onChange={(e) => setTempEmail(e.target.value)} />
      </Form.Group>

      <Form.Group>
        <Form.Label>Password</Form.Label>
        <Form.Control type="password" placeholder="Password" onChange={(e) => setPassword(e.target.value)} />
      </Form.Group>

      <Form.Group>
        <Form.Label>Re-enter Password</Form.Label>
        <Form.Control type="password" placeholder="Re-enter Password" onChange={(e) => setRePassword(e.target.value)} />
      </Form.Group>

      <Button disabled={submitDisabled} onClick={signUp}>Sign Up</Button>
      <Form.Group>
        <Form.Label className="error">{signUpError}</Form.Label>
      </Form.Group>
    </Form>
  );
};

export default SignUp;
