import React, {
  FunctionComponent, ReactElement, useEffect, useState
} from "react";
import Base from "./Base";
import API from "../api";
import Loading from "../Loading/Loading";

const Scenario: FunctionComponent<{
  setView: (val: string) => void,
  progress: number,
  accountId: string,
  setCurrentState: (val: string) => void
}> = ({
  setView,
  progress,
  accountId,
  setCurrentState
}): ReactElement => {
  const [scenario, setScenario] = useState({
    name: "",
    description: "",
    steps: [],
    imgPath: "",
    endpoint: "",
    endpointPath: ""
  });
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    switch (progress) {
      case 0:
        Promise.all([
          import(`./content/${progress + 1}.json`),
          import(`./content/${progress + 1}.png`)
        ]).then(([json, img]) => {
          setScenario({
            name: json.name,
            description: json.description,
            steps: json.steps,
            imgPath: img.default,
            endpoint: json.endpoint,
            endpointPath: json.endpointPath
          });
          setIsLoading(false);
        });
        break;
      default:
        Promise.all([
          API.TearDownEnvironment(),
          API.UpdateParticipantResult()
        ]).then(() => {
          setCurrentState("terminating");
          setView("Finish");
        });
    }
  }, [progress, setView, setCurrentState]);

  return (
    <>
      {
        isLoading && <Loading />
      }
      {
        !isLoading && (
        <Base
          name={scenario.name}
          description={scenario.description}
          steps={scenario.steps}
          setView={setView}
          accountId={accountId}
          imgPath={scenario.imgPath}
          endpoint={scenario.endpoint}
          endpointPath={scenario.endpointPath}
        />
        )
      }
    </>
  );
};

export default Scenario;

