import React, { ReactElement, FunctionComponent, useState } from "react";
import {
  Button,
  Form
} from "react-bootstrap";
import { Auth } from "aws-amplify";

const ConfirmSignUp: FunctionComponent<{
  setView: (val: string) => void,
  email: string
}> = ({ email, setView }): ReactElement => {
  const [code, setCode] = useState("");
  const [signUpError, setSignUpError] = useState("");

  const submitDisabled = !code;

  const confirmSignUp = () => {
    Auth.confirmSignUp(
      email,
      code
    )
      .then((response) => {
        console.log(response);
        setView("SignIn");
      })
      .catch((error) => {
        console.log("error signing up:", error);
        setSignUpError(error.message);
      });
  };

  return (
    <Form className="form">
      <h3>Confirm Sign Up</h3>
      <Form.Group>
        <Form.Label>Code</Form.Label>
        <Form.Control type="text" placeholder="Enter code" onChange={(e) => setCode(e.target.value)} />
      </Form.Group>

      <Button disabled={submitDisabled} onClick={confirmSignUp}>Sign Up</Button>
      <Form.Group>
        <Form.Label className="error">{signUpError}</Form.Label>
      </Form.Group>
    </Form>
  );
};

export default ConfirmSignUp;
